"use client";
import { use } from "react";
import { factories } from "@/lib/factories";
import { matchRequests } from "@/lib/matchRequests";
import { useUser } from "@clerk/nextjs";
import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import Image from "next/image";

export default function FactoryDetailPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params);
  const factory = factories.find(f => f.id === id);
  const { user } = useUser();
  const [showForm, setShowForm] = useState(false);
  const [content, setContent] = useState("");
  const [submitted, setSubmitted] = useState(false);
  if (!factory) {
    return <div className="max-w-xl mx-auto py-10 px-4 text-center text-gray-500">존재하지 않는 공장입니다.</div>;
  }
  const isDesigner = user?.publicMetadata?.role === "designer";
  const alreadyRequested = user && matchRequests.some(r => r.factoryId === factory.id && r.designerUserId === user.id);
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitted(true);
    setShowForm(false);
  };
  const handleKakaoInquiry = () => {
    if (!user) return;
    const inquiry = {
      id: Date.now(),
      userId: user.id,
      factoryId: factory.id,
      factoryName: factory.name,
      date: new Date().toISOString().slice(0, 10),
      status: "카톡 문의 완료",
      method: "카카오톡",
      image: factory.image,
    };
    const prev = JSON.parse(localStorage.getItem("inquiries") || "[]");
    localStorage.setItem("inquiries", JSON.stringify([inquiry, ...prev]));
    window.open(factory.kakaoUrl || "https://open.kakao.com/o/some-link", "_blank");
  };

  // 이미지 갤러리
  const images = factory.images && factory.images.length > 0 ? factory.images : [factory.image];

  return (
    <div className="max-w-[1400px] mx-auto py-10 px-2 md:px-6">
      {/* 이미지 갤러리 */}
      <div className="w-full grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
        {images.map((img, idx) => (
          <div key={img + idx} className="aspect-[4/3] bg-gray-100 rounded-xl overflow-hidden flex items-center justify-center">
            <Image src={img} alt={factory.name + " 이미지"} width={400} height={300} className="object-cover w-full h-full" />
          </div>
        ))}
      </div>
      {/* 2단 레이아웃 */}
      <div className="flex flex-col md:flex-row gap-8">
        {/* 왼쪽: 상세 정보 */}
        <div className="flex-1 min-w-0">
          {/* 업체명, 태그, 문의 버튼 */}
          <div className="flex flex-col md:flex-row md:items-center gap-2 mb-4">
            <span className="inline-block bg-purple-100 text-purple-700 font-bold px-3 py-1 rounded-full text-xs">패턴</span>
            <span className="text-2xl font-bold ml-0 md:ml-4">{factory.name}</span>
            <span className="ml-0 md:ml-4 text-gray-500 text-sm">연락 가능 · 시간 : 연중무휴</span>
            <Button size="sm" className="ml-auto md:ml-4 bg-gray-100 text-gray-800 border border-gray-300 rounded-lg px-4 py-2" onClick={handleKakaoInquiry}>문의하기</Button>
          </div>
          {/* 위치 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">위치</div>
            <div className="text-gray-700 text-sm">{factory.region} {/* 실제 주소 필드가 있다면 여기에 */}</div>
          </div>
          {/* 주요 정보 - 표 형태 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">주요 정보</div>
            <table className="w-full text-sm text-left border-separate border-spacing-y-1">
              <tbody>
                <tr>
                  <th className="font-semibold text-gray-600 w-32 align-top">업태</th>
                  <td>{factory.business_type || '-'}</td>
                </tr>
                <tr>
                  <th className="font-semibold text-gray-600 w-32 align-top">주요품목</th>
                  <td>{Array.isArray(factory.items) && factory.items.length > 0 ? factory.items.join(', ') : '-'}</td>
                </tr>
                <tr>
                  <th className="font-semibold text-gray-600 w-32 align-top">주요원단</th>
                  <td>{factory.main_fabrics || '-'}</td>
                </tr>
                <tr>
                  <th className="font-semibold text-gray-600 w-32 align-top">MOQ(최소 주문 수량)</th>
                  <td>{factory.moq ?? factory.minOrder ?? '-'}</td>
                </tr>
              </tbody>
            </table>
          </div>
          {/* 보유 장비 - Chip 형태 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">보유 장비</div>
            <div className="flex flex-wrap gap-2">
              {factory.equipment
                ? String(factory.equipment).split(/,|\|| /).filter(Boolean).map((item, i) => (
                    <span key={item + i} className="bg-gray-100 rounded px-3 py-1 text-sm">{item}</span>
                  ))
                : <span className="text-gray-400">-</span>}
            </div>
          </div>
          {/* 패턴 장비 - Chip 형태 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">패턴 장비</div>
            <div className="flex flex-wrap gap-2">
              {factory.pattern_machines
                ? String(factory.pattern_machines).split(/,|\|| /).filter(Boolean).map((item, i) => (
                    <span key={item + i} className="bg-gray-100 rounded px-3 py-1 text-sm">{item}</span>
                  ))
                : <span className="text-gray-400">-</span>}
            </div>
          </div>
          {/* 특수 장비 - Chip 형태 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">특수 장비</div>
            <div className="flex flex-wrap gap-2">
              {factory.special_machines
                ? String(factory.special_machines).split(/,|\|| /).filter(Boolean).map((item, i) => (
                    <span key={item + i} className="bg-gray-100 rounded px-3 py-1 text-sm">{item}</span>
                  ))
                : <span className="text-gray-400">-</span>}
            </div>
          </div>
          {/* 제조사 정보 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">제조사 정보</div>
            <div className="flex flex-wrap gap-8 text-center">
              <div>
                <div className="text-xs text-gray-500 mb-1">입력</div>
                <div className="font-bold text-lg">2015-12-10</div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">행정동</div>
                <div className="font-bold text-lg">{factory.admin_district || '-'}</div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">월 CAPA</div>
                <div className="font-bold text-lg">{factory.monthly_capacity || '-'}</div>
              </div>
              <div>
                <div className="text-xs text-gray-500 mb-1">배송</div>
                <div className="font-bold text-lg">{factory.delivery || '-'}</div>
              </div>
            </div>
          </div>
          {/* 소개 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">소개</div>
            <div className="text-sm text-gray-700">{factory.intro || factory.description}</div>
          </div>
          {/* 특수 보유 기술 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">특수 보유 기술</div>
            <div className="text-sm text-gray-700">{factory.processes?.join(', ') || '-'}</div>
          </div>
          {/* 주요 거래처 */}
          <div className="bg-white rounded-xl border p-4 mb-4">
            <div className="font-bold text-gray-700 mb-1">주요 거래처</div>
            <div className="text-sm text-gray-700">{factory.distribution || '-'}</div>
          </div>
          <div className="text-center mt-8">
            <Link href="/factories" className="text-toss-blue hover:underline">← 목록으로 돌아가기</Link>
          </div>
        </div>
        {/* 오른쪽: 문의/상품/옵션 카드 */}
        <div className="w-full md:w-[380px] flex-shrink-0">
          <div className="bg-white rounded-2xl border p-6 mb-6">
            <div className="font-bold text-lg mb-2">{factory.name}</div>
            <div className="text-xs text-gray-500 mb-2">봉제공장</div>
            <div className="mb-4">
              <div className="font-semibold">Standard</div>
              <ul className="text-sm text-gray-700 list-disc pl-5 mb-2">
                <li>텍스타일 사입 1종</li>
                <li>출고전 재작</li>
                <li>평생 A/S</li>
                <li>원본, 저작, 재설계 이전</li>
                <li>샘플비 10,000원</li>
                <li>장단 단가 16,800원</li>
              </ul>
              <Button className="w-full bg-black text-white rounded-lg font-bold py-2 mt-2">공장 의뢰하기</Button>
            </div>
            <div className="font-semibold mt-4">Deluxe</div>
            <div className="text-sm text-gray-700 mb-2">패턴/샘플 + 공장</div>
            <div className="font-semibold mt-4">Premium</div>
            <div className="text-sm text-gray-700 mb-2">올인원(기획/디자인~)</div>
            <Button className="w-full bg-yellow-400 text-black rounded-full font-bold py-2 mt-4">문의하기</Button>
          </div>
        </div>
      </div>
    </div>
  );
} 